import React, { memo, useEffect, useRef, useState } from 'react';
import {
  TitleStyled,
  TransitionGroupStyled,
} from '@@components/UI/Title/Title.styled';
import { CSSTransition } from 'react-transition-group';
import classNames from 'classnames';

export const Title = memo(
  ({ text = '', size = 'H_15', color = 'GREEN', className = '' }) => {
    const [textArray, setTextArray] = useState([{ item: text, id: 1, color }]);
    const count = useRef(0);

    useEffect(() => {
      count.current += 1;
      if (count.current === 1) {
        return;
      }
      setTextArray([{ item: text, id: count.current, color }]);
    }, [text]);

    return (
      <TransitionGroupStyled>
        {textArray.map((item) => (
          <CSSTransition key={item.id} timeout={500} classNames="fade">
            <TitleStyled
              size={size}
              className={classNames(
                'title',
                item?.color.toLowerCase(),
                className,
              )}
              dangerouslySetInnerHTML={{ __html: item.item }}
            />
          </CSSTransition>
        ))}
      </TransitionGroupStyled>
    );
  },
);
