import React from 'react';
import useRouter from '@@contexts/router/hooks/useRouter';
import {
  SCAN_RESULTS_ROUTE,
  ANTIFRAUDE_ROUTE,
  DOMAIN_SCAN_ROUTE,
  LOADING_SCAN_ROUTE,
  SCAN_ERROR_ROUTE,
  NON_ECOMMERCE_ROUTE,
  BEFORE_RESULTS_ROUTER,
  LOADING_SCAN_INFO_ROUTE,
  PHISHING_EXCLUSION_ROUTE,
  PHISHING_EXCLUSION_RESULT_ROUTE,
  PHISHING_EXCLUSION_SCAN_ROUTE,
  PRODUCT_DELIVERY_LOAD_ROUTE,
  PRODUCT_DELIVERY_VIEW_ROUTE,
  AFTER_5_DAY_EMAIL,
  AFTER_5_DAY_VISIT,
  AFTER_1_DAY_VISIT,
  AFTER_14_DAY_EMAIL,
  AFTER_13_DAY_VISIT_FIRST,
  AFTER_13_DAY_VISIT_SECOND,
  AFTER_14_DAY_DANGEROUS_FIRST,
  AFTER_14_DAY_DANGEROUS_SECOND,
  AFTER_15_DAY_EMAIL,
  INIT_AUTO_APPLY_ROUTE,
  NON_GREEN_LOADING_SCAN_ROUTE,
  AUTH_OFFERS_ROUTE,
  PROMOTION_TARGET_USER_EMAIL,
  PROMOTION_TARGET_USER_VISIT,
  COOKIE_MANAGER_ROUTE,
} from '@@constants/routes';
import styled from 'styled-components';
import useToaster from '@@contexts/toaster/hooks/useToaster';

const ROUTES = [
  PROMOTION_TARGET_USER_EMAIL,
  PROMOTION_TARGET_USER_VISIT,
  AFTER_1_DAY_VISIT,
  AFTER_5_DAY_EMAIL,
  AFTER_5_DAY_VISIT,
  AFTER_13_DAY_VISIT_FIRST,
  AFTER_13_DAY_VISIT_SECOND,
  AFTER_14_DAY_EMAIL,
  AFTER_14_DAY_DANGEROUS_FIRST,
  AFTER_14_DAY_DANGEROUS_SECOND,
  AFTER_15_DAY_EMAIL,

  ANTIFRAUDE_ROUTE,
  SCAN_RESULTS_ROUTE,

  DOMAIN_SCAN_ROUTE,
  LOADING_SCAN_INFO_ROUTE,
  LOADING_SCAN_ROUTE,
  NON_GREEN_LOADING_SCAN_ROUTE,
  BEFORE_RESULTS_ROUTER,

  SCAN_ERROR_ROUTE,
  NON_ECOMMERCE_ROUTE,

  PHISHING_EXCLUSION_ROUTE,
  PHISHING_EXCLUSION_RESULT_ROUTE,
  PHISHING_EXCLUSION_SCAN_ROUTE,

  PRODUCT_DELIVERY_LOAD_ROUTE,
  PRODUCT_DELIVERY_VIEW_ROUTE,
  INIT_AUTO_APPLY_ROUTE,

  AUTH_OFFERS_ROUTE,
];

const MainStyled = styled.div`
  padding: 16px;
  overflow-y: scroll;
  max-height: ${({ theme }) => theme.contentMaxHeight - 16 - 44}px;
`;

export const Main = () => {
  const router = useRouter();
  const { showCookie } = useToaster();

  const handlerClick = (page) => {
    router.changePage(page);
  };

  const handlerOpenModal = () => {
    showCookie(true);
  };

  return (
    <MainStyled>
      <div style={{ marginBottom: 8 }}>
        <button type="button" onClick={handlerOpenModal}>
          {COOKIE_MANAGER_ROUTE}
        </button>
      </div>
      {ROUTES.map((route, id) => (
        <div key={id} style={{ marginBottom: 8 }}>
          <button type="button" onClick={() => handlerClick(route)}>
            {route}
          </button>
        </div>
      ))}
    </MainStyled>
  );
};
