import React, { useEffect, useRef } from 'react';
import { ThreeSparkle } from '@@components/UI/ThreeSparkle';
import {
  SOCIAL_BUTTONS_TYPE,
  SocialButton,
} from '@@components/UI/SocialButton';
import { getUseProfile } from '@@utils/authorization/getUseProfile';
import { savingUserDate } from '@@utils/authorization/savingUserDate';
import useProfile from '@@contexts/profile/hooks/useProfile';
import useRouter from '@@contexts/router/hooks/useRouter';
import useToaster from '@@contexts/toaster/hooks/useToaster';
import { openAuthWindow } from '@@utils/authorization/openAuthWindow';
import { useTranslation } from '@@hooks/useTranslation';
import { sendUUID } from '@@content/Toaster/toasterLogicParts/AutoToaster/sendUUID';
import { resetCashedData } from '@@utils/authorization/resetCashedData';
import { AUTH_DATA } from '@@content/Toaster/pages/Authorization/data';
import { SITE_URL } from '@@constants/common';
import {
  AlternativeAuthDescription,
  AlternativeAuthSTitle,
  AlternativeSocialContent,
  AuthorizationStyled,
} from './Authorization.styled';

export const AUTH_TYPE = {
  OFFERS: 'offers',
  ALTERNATIVE_PRODUCT: 'alternativeProduct',
  PRICE_COMPARISON: 'priceComparison',
};

export const Authorization = ({ type = AUTH_TYPE.OFFERS }) => {
  const router = useRouter();
  const toaster = useToaster();
  const profile = useProfile();
  const { t } = useTranslation();
  const tabRef = useRef(null);

  const data = AUTH_DATA[type];

  const domain = toaster.currentDomain;

  useEffect(() => {
    const onTalkWithWindow = async (event) => {
      const site =
        event?.origin?.includes(SITE_URL) ||
        event?.origin?.includes('localhost:3000');

      if (!site) {
        return;
      }
      const dataParse = JSON.parse(event?.data);

      if (!dataParse?.token) {
        return;
      }

      const res = await getUseProfile(dataParse?.token);
      if (res?.code > 201) {
        return;
      }

      const user = await savingUserDate(res);
      sendUUID();
      profile.setProfileUser(user);
      const { offers } = await resetCashedData(domain);
      toaster.resetOffersCache({ ...offers });
      tabRef.current = null;
      router.goBack();
    };

    window.addEventListener('message', onTalkWithWindow);
    return () => {
      window.removeEventListener('message', onTalkWithWindow);
    };
  }, []);

  return (
    <AuthorizationStyled>
      <ThreeSparkle>{data.icon}</ThreeSparkle>
      <AlternativeAuthSTitle
        dangerouslySetInnerHTML={{
          __html: t(data.title),
        }}
      />
      <AlternativeAuthDescription
        dangerouslySetInnerHTML={{
          __html: t(data.description),
        }}
      />
      <AlternativeSocialContent>
        <p className="social__title">{t('alternativeAuthSocialTitle')}</p>
        <div className="social__buttons-wrap">
          <SocialButton
            type={SOCIAL_BUTTONS_TYPE.GOOGLE}
            onClick={() => {
              openAuthWindow('google', tabRef);
            }}
          />
          <SocialButton
            type={SOCIAL_BUTTONS_TYPE.FACEBOOK}
            onClick={() => {
              openAuthWindow('facebook', tabRef);
            }}
          />
        </div>
        <div className="social__separator">
          <span />
          <p>{t('alternativeAuthSeparator')}</p>
          <span />
        </div>
        <SocialButton
          type={SOCIAL_BUTTONS_TYPE.EMAIL}
          onClick={() => {
            openAuthWindow('email', tabRef);
          }}
        />
      </AlternativeSocialContent>
    </AuthorizationStyled>
  );
};
