import React, { memo, useEffect } from 'react';
import { Animation } from '@@components/UI/Animation';

import { Button, BUTTON_TYPES } from '@@components/UI/Button';

import DangerIcon from '@@svg/danger.svg';
import WarningIcon from '@@svg/warning.svg';
import CloseIcon from '@@svg/close.svg';

import { PROTECT_URL, PROTECTION_EMAIL_URL } from '@@constants/common';
import { objectToQuery } from '@@utils/utils';
import useToaster from '@@contexts/toaster/hooks/useToaster';
import { sendMessageToBg } from '@@utils/extensionScripts';
import { BG_SAVE_DANGEROUS_NOTIFICATION } from '@@constants/backgroundEvents';
import { StolenDataStyled } from './StolenData.styled';

const t = {
  cybercriminal: {
    disabled: true,
    active: false,
  },

  email: {
    disabled: true,
    active: true,
  },
};
const POPUP_DELAY = 300;
const POPUP_DATA = {
  email: {
    dataType: 'DangerEmail',
    title: 'VOTRE BOÎTE EMAIL',
    subTitle: 'EST EXPOSÉE À DES CYBERCRIMINELS.',
    description:
      'Votre sécurité pourrait être compromise suite à de récentes fuites de données ayant exposé des informations sensibles vous concernant.',
    button: 'Bloquer ces menaces',
    url: PROTECTION_EMAIL_URL,
  },

  cybercrime: {
    dataType: 'CyberCrime',
    title: 'Des CYBERCRIMINEL',
    subTitle: 'ont volé CERTAINES DE vos données.',
    description:
      'Procédez tout de suite à un diagnostic complet pour rechercher vos données corrompues sur le darkweb.',
    button: 'Lancer un diagnostic',
    url: PROTECT_URL,
  },
};

export const StolenData = memo(() => {
  const { openDangerous, dangerousType: type, showDangerous } = useToaster();

  const data = POPUP_DATA?.[type] || {};

  const handlerOpen = () => {
    const params = {
      notification_name: type,
    };

    const url = `${data.url}?${objectToQuery(params)}`;
    window.open(url);
    showDangerous(false);
  };

  const handlerClose = () => {
    showDangerous(false);
  };

  useEffect(() => {
    if (openDangerous) {
      sendMessageToBg({
        event: BG_SAVE_DANGEROUS_NOTIFICATION,
        type,
      });
    }
  }, [openDangerous, data]);

  return (
    <Animation opened={openDangerous} delay={POPUP_DELAY}>
      <StolenDataStyled>
        <button
          type="button"
          aria-label="Close button"
          className="close"
          onClick={handlerClose}
        >
          <CloseIcon width={24} height={24} fill="currentColor" />
        </button>

        <div className="content">
          <WarningIcon width={76} height={76} fill="#A90A0A" />
          <div className="wrap">
            <h1 className="title">
              {data.title}
              <br />
              <span>{data.subTitle}</span>
            </h1>

            <h2 className="subTitle">
              <DangerIcon width={17} height={20} fill="#A90A0A" />
              santé système
            </h2>
            <p className="description">{data.description}</p>
            <Button
              type={BUTTON_TYPES.TRANSPARENT}
              width="193px"
              height="38px"
              onClick={handlerOpen}
              isCta={false}
            >
              {data.button}
            </Button>
          </div>
        </div>
      </StolenDataStyled>
    </Animation>
  );
});
