import React, { memo, useState } from 'react';
import classnames from 'classnames';
import { dateFormat } from '@@utils/utils';
import ShowMoreIcon from '@@svg/arrowDown.svg';
import useToaster from '@@contexts/toaster/hooks/useToaster';

import { SimpleBlock } from '../datapoints/SimpleBlock';
import { CountryBlock } from '../datapoints/CountryBlock';
import { TechBlock, TechMore } from '../datapoints/TechBlock';
import { PaymentBlock, PaymentMore } from '../datapoints/PaymentBlock';
import { SocialBlock, SocialMore } from '../datapoints/SocialBlock';
import { HasMXBlock } from '../datapoints/HasMXBlock';
import { AdressBlock, AdressMore } from '../datapoints/AdressBlock';
import { ReviewBlock } from '../datapoints/ReviewBlock';
import { TeamAnalysis } from '../datapoints/TeamAnalysis';
import {
  LegalNoticeBlock,
  LegalNoticeMore,
} from '../datapoints/LegalNoticeBlock';
import { SirenVatBlock } from '../datapoints/SirenVatBlock';
import { MetaTagsBlock, MetaTagsMore } from '../datapoints/MetaTagsBlock';
import { BacklinksBlockBlock } from '../datapoints/BacklinksBlock';
import { HttpsBlock, HttpsMore } from '../datapoints/HttpsBlock';
import {
  UserFeedbackRaiting,
  UserFeedbackRaitingMore,
} from '../datapoints/UserFeedbackRaiting';
import {
  MainResultBlockContainer,
  MainResultBlockContent,
  MoreBlockContainer,
} from './MainResultBlock.styled';

const DataPointBlock = memo(({ data }) => {
  const { result, name } = data;

  switch (name) {
    case 'sirenVat':
      return <SirenVatBlock title={name} result={result} />;
    case 'legalNotice':
      return <LegalNoticeBlock title={name} result={result} />;
    case 'reviews':
      return <ReviewBlock title={name} result={result} />;
    case 'country':
      return <CountryBlock title={name} country={result} />;
    case 'fraudAlerts':
      return <SimpleBlock title={name} count={result} />;
    case 'metaTags':
      return <MetaTagsBlock title={name} result={result} />;
    case 'teamAnalysis':
      return <TeamAnalysis title={name} />;
    case 'technologiesStack':
      return <TechBlock title={name} tech={result} />;
    case 'paymentStack':
      return <PaymentBlock title={name} payments={result} />;
    case 'socialStack':
      return <SocialBlock title={name} socials={result} />;
    case 'hasMX':
    case 'visibility':
      return <HasMXBlock title={name} result={result} />;
    case 'https':
      return <HttpsBlock title={name} result={result} />;
    case 'physicalAddress':
      return <AdressBlock title={name} result={result} />;
    case 'userFeedbackRating':
      return <UserFeedbackRaiting title={name} />;
    case 'backlinks':
      return <BacklinksBlockBlock title={name} count={result} />;
    case 'registryUpdatedDate':
      return (
        <AdressBlock title={name} result={result ? dateFormat(result) : null} />
      );

    case 'expire':
    case 'creation':
      return (
        <AdressBlock title={name} result={result ? dateFormat(result) : null} />
      );

    case 'alexaRank':
    case 'trancoRank': {
      let count = '1';
      try {
        if (data?.ratingValue || data?.result?.ratingValue) {
          count = result?.ratingValue || data?.ratingValue;
        } else if (result) {
          const res = result?.result || result;
          const maxValue = 8000000;
          count = 100 - (res * 100) / maxValue;
          count = res >= maxValue ? 1 : count;
        }

        const cutCount = count?.toString()?.split('.');
        if (cutCount[1]?.length > 1) {
          count = `${cutCount[0]}.${cutCount[1].substring(0, 1)}`;
        }

        count = Math.abs(+count)
          .toString()
          .replace(/(\.0|\.00)$/, '')
          .match(/^([0-9]{1,3})*([.,][0-9])?$/)[0];
        count = count >= 1 ? count : 1;
        count = count > 100 ? 100 : count;
      } catch (e) {
        console.warn('ERROR DataPointBlock alexaRank: ', e.messagexw);
      }
      return <SimpleBlock title={name} count={count} />;
    }

    default:
      return null;
  }
});

const MoreBlock = memo(({ data }) => {
  const { result, name } = data;

  switch (name) {
    case 'reviews':
    case 'alexaRank':
    case 'trancoRank':
    case 'hasMX':
    case 'fraudAlerts':
    case 'visibility':
    case 'creation':
    case 'expire':
    case 'country':
    case 'teamAnalysis':
    case 'sirenVat':
    case 'backlinks':
    case 'registryUpdatedDate':
      return <AdressMore title={name} />;
    case 'userFeedbackRating':
      return <UserFeedbackRaitingMore title={name} result={result} />;
    case 'https':
      return <HttpsMore title={name} result={result} />;
    case 'metaTags':
      return <MetaTagsMore title={name} result={result} />;
    case 'legalNotice':
      return <LegalNoticeMore title={name} result={result} />;
    case 'physicalAddress':
      return <AdressMore title={name} result={result} />;
    case 'technologiesStack':
      return <TechMore title={name} tech={result} />;
    case 'paymentStack':
      return <PaymentMore title={name} payments={result} />;
    case 'socialStack':
      return <SocialMore title={name} socials={result} />;
    default:
      return null;
  }
});

export const MainResultBlock = memo(({ data }) => {
  const toaster = useToaster();
  const [openMore, setOpenMore] = useState(false);

  const color = toaster.domainColor?.toLowerCase() || null;
  const openMoreBlockHandler = () => {
    setOpenMore((prevState) => !prevState);
  };

  return (
    <MainResultBlockContainer className={classnames({ openMore })}>
      <MainResultBlockContent onClick={openMoreBlockHandler}>
        <DataPointBlock openMore={openMore} data={data} color={color} />
        <button
          type="button"
          aria-label="More datapoint information"
          className={classnames('datapoint-showMore--button', {
            opened: openMore,
          })}
        >
          <ShowMoreIcon width={24} height={24} fill="currentColor" />
        </button>
      </MainResultBlockContent>

      <MoreBlockContainer className={classnames({ openMore })}>
        <div className="show-more--container">
          <MoreBlock data={data} color={color} />
        </div>
      </MoreBlockContainer>
    </MainResultBlockContainer>
  );
});
