import React, { memo } from 'react';
import { OverlayStyled } from '@@components/UI/Overlay/Overlay.styled';
import { Animation } from '@@components/UI/Animation';

export const Overlay = memo(
  ({ opened = false, delay = 300, background = 'rgba(0, 0, 0, 0.7)' }) => (
    <Animation opened={opened} delay={delay}>
      <OverlayStyled background={background} />
    </Animation>
  ),
);
