export const BeforeResultsInfo = ({ phishingExclusion }) => {
  const router = useRouter();
  const toaster = useToaster();
  const hasAffiliateLink = Boolean(toaster?.hasAffiliate);

  const { handlerCloseToaster } = useAntifraude();

  const color =
    toaster.domainColor?.toLowerCase() ||
    toaster.scanResult?.ratingColor?.toLowerCase() ||
    null;
  const isGreen = color === 'green';
  const content = CONTENT_TYPES[color];

  const handlerResultClick = () => {
    if (toaster?.scanResult?.isDomainCacheExpired) {
      router.changePage(NON_GREEN_LOADING_SCAN_ROUTE);
      return;
    }

    router.changePage(SCAN_RESULTS_ROUTE);
  };

  const handlerCTAClick = async () => {
    const defaultRoute = phishingExclusion
      ? PHISHING_EXCLUSION_RESULT_ROUTE
      : BEFORE_RESULTS_ROUTER;

    handlerCloseToaster({ defaultRoute });
  };

  return (
    <BeforeResultsInfoStyled>
      <div className="scan-domain__content">
        <div className="scan-domain__title-content">
          <AnimatedIcon
            icon={ANIMATED_ICONS.security}
            color={color?.toUpperCase()}
            size={60}
          />
          <h1 className={`scan-domain__title ${color}`}>
            {content?.title}
            <br />
            <span className={`scan-domain__description ${color}`}>
              {parse(content?.description)}
            </span>
          </h1>
        </div>
        {hasAffiliateLink && (
          <Button
            type={BUTTON_TYPES.GREEN}
            className={classnames('scan-domain__affiliate-button')}
            onClick={handlerCTAClick}
            width="auto"
            isSmall
          >
            <span>Continuer ma navigation</span>
          </Button>
        )}
      </div>

      <Button
        type={BUTTON_TYPES.OUTLINE}
        className="scan-domain__result-button"
        onClick={handlerResultClick}
        isSmall
      >
        {isGreen ? 'Voir les 20 informations' : 'Voir le détail'}
        <ArrowIcon width={24} height={24} fill="currentColor" />
      </Button>
    </BeforeResultsInfoStyled>
  );
};
