import React from 'react';
import { Button, BUTTON_TYPES } from '@@components/UI/Button';
import { FvPlusPageStyled } from '@@components/FvPlus/FvPlusPage/FvPlusPage.styled';
import useToaster from '@@contexts/toaster/hooks/useToaster';
import { PROMOTION_TARGET_URL, PROTECT_RESULT_URL } from '@@constants/common';
import useRouter from '@@contexts/router/hooks/useRouter';
import { objectToQuery } from '@@utils/utils';
import {
  PROMOTION_TARGET_USER_EMAIL,
  PROMOTION_TARGET_USER_VISIT,
} from '@@constants/routes';
import { useSetAutoToaster } from '@@hooks/useSetAutoToaster';
import parse from 'html-react-parser';

const PROMOTION_TARGET_TOASTERS = [
  PROMOTION_TARGET_USER_EMAIL,
  PROMOTION_TARGET_USER_VISIT,
];

export const FvPlusPage = ({ title, description, button, icon, onClick }) => {
  const { toggleAlert } = useToaster();
  const { currentPage } = useRouter();
  const handlerSetAutoToaster = useSetAutoToaster();

  const handlerClick = () => {
    if (onClick) {
      onClick();
      return;
    }

    const isPromotionTargetToaster =
      PROMOTION_TARGET_TOASTERS.includes(currentPage);

    if (isPromotionTargetToaster) {
      handlerSetAutoToaster();
      window.open(PROMOTION_TARGET_URL);
      toggleAlert();
      return;
    }
    toggleAlert();

    const notificationName = currentPage.replace(/\//, '');
    const params = {
      redirect: true,
      notification_name: notificationName,
    };
    const url = `${PROTECT_RESULT_URL}?${objectToQuery(params)}`;
    window.open(url);
  };

  return (
    <FvPlusPageStyled>
      {icon}
      <h2 className="title">{parse(title)}</h2>
      <p className="description">{description}</p>
      <Button
        type={BUTTON_TYPES.BLUE}
        width="fit-content"
        height="50px"
        margin="0 auto"
        onClick={handlerClick}
      >
        {button}
      </Button>
    </FvPlusPageStyled>
  );
};
