import React from 'react';

import { Button, BUTTON_TYPES } from '@@components/UI/Button';
import { getPublicUrl } from '@@utils/utils';

import { useCookieFunctionality } from '@@hooks/useCookieFunctionality';
import { Animation } from '@@components/UI/Animation';
import useToaster from '@@contexts/toaster/hooks/useToaster';

import { Overlay } from '@@components/UI/Overlay';
import { CookieManagerStyled } from './CookieManager.styled';

export const CookieManager = () => {
  const { handlerAccept, handlerReject } = useCookieFunctionality();
  const { openCookie } = useToaster();

  return (
    <>
      <Overlay
        opened={openCookie}
        delay={300}
        background="rgba(0, 0, 0, 0.7)"
      />
      <Animation opened={openCookie} delay={300}>
        <CookieManagerStyled>
          <div className=" cookie__banner header">
            <div>
              <h1 className="cookie__title">Protéger ma navigation</h1>
              <p className="cookie__description">
                FranceVerif s’occupe de bloquer tous les cookies traceurs pour
                vous
              </p>
            </div>
            <img
              className="image"
              src={getPublicUrl('images/cookie.png')}
              width={65}
              height={65}
              alt="Cookie"
            />
          </div>
          <div className=" cookie__banner">
            <Button
              type={BUTTON_TYPES.BLUE}
              width="100%"
              onClick={handlerAccept}
              isCta={false}
            >
              Bloquer tous les cookies
            </Button>
            <Button
              type={BUTTON_TYPES.WHITE}
              width="100%"
              onClick={handlerReject}
              isCta={false}
            >
              Non, merci.
            </Button>
          </div>
        </CookieManagerStyled>
      </Animation>
    </>
  );
};
