import React, { useRef } from 'react';
import { callBrowser } from '@@utils/utils';
import { Button, BUTTON_TYPES } from '@@components/UI/Button';
import { useTranslation } from '@@hooks/useTranslation';
import { NotFoundBestPrice } from '@@components/UI/Checkout/Not-found/Not-found';
import { closeModal } from '@@utils/checkout/closeModal';
import useToaster from '@@contexts/toaster/hooks/useToaster';
import {
  PRODUCT_DELIVERY_VIEW_ROUTE,
  ACTIVE_CASHBACK_ROUTE,
} from '@@constants/routes';
import useRouter from '@@contexts/router/hooks/useRouter';
import { Sparkle } from '@@components/UI/AnimationIcons/Sparkle';
import { useResultApplyAffiliateActivated } from '@@hooks/useResultApplyAffiliateActivated';
import { AlreadyBestPrice } from '@@components/UI/Checkout/AlreadyBestPrice';
import { FindBestPrice } from '@@components/UI/Checkout/FindBestPrice';
import { FindBestPriceWithCashback } from '@@components/UI/Checkout/FindBestPriceWithCashback';
import { ANIMATION_DELAY } from '@@constants/anomationDelay';
import { SITE_URL } from '@@constants/common';
import { AUTO_APPLY_RESULT_TYPES } from '@@constants/applyCouponResultTypes';
import parse from 'html-react-parser';
import { ResultsAutoApplyStyled } from './ResultsAutoApply.styled';

export const ResultsAutoApply = ({ type }) => {
  const { t } = useTranslation();
  const toaster = useToaster();
  const router = useRouter();
  const ref = useRef();

  const { handlerAffiliateActivated } = useResultApplyAffiliateActivated();
  const uniqueIdFriendly = toaster?.domainOption?.uniqueIdFriendly || true;
  const isAvailableCashback = toaster?.offersCache?.cashback?.length;

  const showOfTypeResult = (sectionType) => {
    switch (sectionType) {
      case AUTO_APPLY_RESULT_TYPES.FIND_BEST_PRICE_WITH_CASHBACK:
        return <FindBestPriceWithCashback />;
      case AUTO_APPLY_RESULT_TYPES.FIND_BEST_PRICE:
        return <FindBestPrice />;
      case AUTO_APPLY_RESULT_TYPES.ALREADY_BEST_PRICE:
        return <AlreadyBestPrice />;
      default:
        return <NotFoundBestPrice />;
    }
  };

  const onClickHandler = async () => {
    toaster.showToaster(false);
    closeModal();
    const openNextToaster = await handlerAffiliateActivated();

    setTimeout(() => {
      setTimeout(() => {
        toaster.showToaster(openNextToaster);
      }, ANIMATION_DELAY.TOASTER_TOGGLE);

      router.changePage(PRODUCT_DELIVERY_VIEW_ROUTE);
    }, ANIMATION_DELAY.WIDGET_SHOW);
  };

  return (
    <ResultsAutoApplyStyled ref={ref}>
      {type === AUTO_APPLY_RESULT_TYPES.ALREADY_BEST_PRICE ? (
        <div className="already-additional-title">
          {parse(t('successAutopApplyAlredyTitle'))}
        </div>
      ) : null}
      <div className="money">
        <div className="money-wrap">
          <img
            className="image"
            src={callBrowser().runtime.getURL(`images/big-money.png`)}
            width={167}
            height={126}
            alt="Money"
          />
          <Sparkle size={20} rotate={-6.37} className="first-sparkle" />
          <Sparkle size={30} rotate={-6.37} className="second-sparkle" />
          <Sparkle size={45} rotate={37.012} className="third-sparkle" />
        </div>
      </div>
      {showOfTypeResult(type)}
      <div className="button">
        <Button type={BUTTON_TYPES.GREEN} onClick={onClickHandler}>
          {t('successAutoApplyButton')}
        </Button>
      </div>
      {type === AUTO_APPLY_RESULT_TYPES.ALREADY_BEST_PRICE ? (
        <div className="additionalLink">
          {parse(t('politiqueConfidentialite', SITE_URL))}
        </div>
      ) : null}
    </ResultsAutoApplyStyled>
  );
};
