import styled from 'styled-components';
import { TransitionGroup } from 'react-transition-group';

export const TransitionGroupStyled = styled(TransitionGroup)`
  position: relative;
  width: 100%;
  height: auto;
  display: flex;
  &.default {
    .shear-animation-enter {
      transform: translateX(100%);
    }

    .shear-animation-enter-active {
      transform: translateX(0);
      transition: transform ${({ time }) => time || 0}ms;
    }

    .shear-animation-exit {
      transform: translateX(0);
      position: absolute;
    }

    .shear-animation-exit-active {
      position: absolute;
      transform: translateX(-100%);
      transition: transform ${({ time }) => time || 0}ms;
    }
  }
  &.revers {
    .shear-animation-enter {
      transform: translateX(-100%);
    }

    .shear-animation-enter-active {
      transform: translateX(0);
      transition: transform ${({ time }) => time || 0}ms;
    }

    .shear-animation-exit {
      transform: translateX(0);
      position: absolute;
    }

    .shear-animation-exit-active {
      position: absolute;
      transform: translateX(100%);
      transition: transform ${({ time }) => time || 0}ms;
    }
  }
`;
export const ContentWrap = styled.div`
  position: relative;
  width: 100%;
  top: 0;
  left: 0;
  height: fit-content;
  flex-shrink: 0;
  transform: translateX(0);
`;
