import React from 'react';
import { useTranslation } from '@@hooks/useTranslation';
import useToaster from '@@contexts/toaster/hooks/useToaster';
import { AlreadyBestPriceStyled } from './AlreadyBestPrice.styled';

export const AlreadyBestPrice = () => {
  const { t } = useTranslation();
  const toaster = useToaster();

  const cashback = toaster?.offersCache?.cashback?.[0]?.formattedAmount || '0%';

  return (
    <AlreadyBestPriceStyled
      dangerouslySetInnerHTML={{
        __html: t('successAutopApplyAlredyTextCashback', cashback),
      }}
    />
  );
};
