import React, { useEffect, useState } from 'react';
import classNames from 'classnames';
import useToaster from '@@contexts/toaster/hooks/useToaster';
import useRouter from '@@contexts/router/hooks/useRouter';
import { DraggableLayout } from '@@components/DraggableLoyaout';
import { Header } from '@@components/UI/Header';

import { ShearAnimation } from '@@components/UI/ShearAnimation';
import { Animation } from '@@components/UI/Animation';
import { ThemeProvider } from 'styled-components';
import { useLogout } from '@@hooks/useLogout';
import { useMassageListener } from '@@hooks/useMassageListener';
import { sendMessageToBg } from '@@utils/extensionScripts';
import {
  ALREADY_BEST_PRICE_AUTO_APPLY_ROUTE,
  ANTIFRAUDE_ROUTE,
  BEFORE_RESULTS_ROUTER,
  BEST_PRICE_AUTO_APPLY_ROUTE,
  BEST_PRICE_WITH_CASHBACK_AUTO_APPLY_ROUTE,
  COOKIE_MANAGER_ROUTE,
  DOMAIN_SCAN_ROUTE,
  HELP_ROUTE,
  INIT_AUTO_APPLY_ROUTE,
  LEGAL_INFORMATION_ROUTE,
  LOADING_AUTO_APPLY_ROUTE,
  LOADING_SCAN_INFO_ROUTE,
  LOADING_SCAN_ROUTE,
  MAIN_ROUTE,
  NON_ECOMMERCE_ROUTE,
  NON_GREEN_LOADING_SCAN_ROUTE,
  NOT_FOUND_AUTO_APPLY_ROUTE,
  PHISHING_EXCLUSION_RESULT_ROUTE,
  PHISHING_EXCLUSION_ROUTE,
  PHISHING_EXCLUSION_SCAN_ROUTE,
  PRODUCT_DELIVERY_LOAD_ROUTE,
  PRODUCT_DELIVERY_VIEW_ROUTE,
  PROMOTION_TARGET_USER_EMAIL,
  PROMOTION_TARGET_USER_VISIT,
  SCAN_ERROR_ROUTE,
  SCAN_RESULTS_ROUTE,
} from '@@constants/routes';
import { TooltipProvider } from '@@components/Tooltip';
import { getToasterType } from '@@utils/utils';
import { ANIMATION_DELAY } from '@@constants/anomationDelay';
import { useAnimationFreeze } from '@@hooks/useAnimationFreeze';
import { useSiteMessageListener } from '@@hooks/useSiteMessageListner';
import { useCookieManger } from '@@hooks/useCookieManger';
import { CookieManager } from '@@content/Toaster/pages/CookieManager';
import { Portal } from '@@components/UI/Portal/Portal';
import { StolenData } from '@@components/StolenData/StolenData';
import {
  ContentWrap,
  ToasterPosition,
  ToasterStyled,
  Wrap,
} from './Toaster.styled';

const TOASTER_WIDTH = {
  [HELP_ROUTE]: 372,
  [LEGAL_INFORMATION_ROUTE]: 320,

  [ANTIFRAUDE_ROUTE]: 338,
  [SCAN_RESULTS_ROUTE]: 385,

  [DOMAIN_SCAN_ROUTE]: 390,
  [LOADING_SCAN_INFO_ROUTE]: 361,
  [LOADING_SCAN_ROUTE]: 334,
  [NON_GREEN_LOADING_SCAN_ROUTE]: 334,
  [BEFORE_RESULTS_ROUTER]: 352,

  [SCAN_ERROR_ROUTE]: 339,
  [NON_ECOMMERCE_ROUTE]: 339,

  [PHISHING_EXCLUSION_ROUTE]: 400,
  [PHISHING_EXCLUSION_RESULT_ROUTE]: 352,
  [PHISHING_EXCLUSION_SCAN_ROUTE]: 334,

  [PRODUCT_DELIVERY_LOAD_ROUTE]: 300,
  [PRODUCT_DELIVERY_VIEW_ROUTE]: 322,
  [INIT_AUTO_APPLY_ROUTE]: 358,

  [PROMOTION_TARGET_USER_EMAIL]: 320 + 32,
  [PROMOTION_TARGET_USER_VISIT]: 320 + 32,

  [LOADING_AUTO_APPLY_ROUTE]: 440,
  [BEST_PRICE_WITH_CASHBACK_AUTO_APPLY_ROUTE]: 440,
  [BEST_PRICE_AUTO_APPLY_ROUTE]: 440,
  [ALREADY_BEST_PRICE_AUTO_APPLY_ROUTE]: 440,
  [NOT_FOUND_AUTO_APPLY_ROUTE]: 440,

  [COOKIE_MANAGER_ROUTE]: 480 + 32,
};

export const Toaster = () => {
  const { currentPage, history } = useRouter();
  const toaster = useToaster();

  const isDisableAnimation = toaster.animationFreeze > 5;

  const [isDrag, setIsDrag] = useState(false);
  const [toasterPosition, setToasterPosition] = useState('bottom');
  const [contentHeight, setContentHeight] = useState(null);
  const [contentMaxHeight, setContentMaxHeight] = useState(null);
  console.debug('toaster=========>', toaster);
  console.debug('HISTORY=========>', history);

  // ---------- LOG OUT FUNCTIONALITY ----------
  useLogout();

  // ---------- MESSAGE LISTENER FUNCTIONALITY ----------
  useMassageListener();

  // ---------- SITE MESSAGE LISTENER FUNCTIONALITY ----------
  useSiteMessageListener();

  useCookieManger();

  const onEndAnimation = (isFreezeAnimation) => {
    let freezeCount = 0;

    if (isFreezeAnimation) {
      freezeCount = toaster.animationFreeze + 1;
    }

    toaster.setAnimationFreeze(freezeCount);
  };
  const { ref } = useAnimationFreeze(onEndAnimation, {
    shift: 3,
    disabled: isDisableAnimation,
  });

  const toasterWidth = TOASTER_WIDTH?.[currentPage] || 399;
  const toasterClass = currentPage.replace(/\//, '');

  const { className: toasterType } = getToasterType(currentPage);
  return (
    <DraggableLayout
      setIsDrag={setIsDrag}
      setToasterPosition={setToasterPosition}
      currentPage={currentPage}
      setContentMaxHeight={setContentMaxHeight}
    >
      <Wrap>
        <Portal>
          <StolenData />
        </Portal>

        <Portal>
          <CookieManager />
        </Portal>
        <ToasterPosition
          className={classNames('toaster-position', toasterPosition)}
        >
          <Animation
            opened={toaster?.openAlert || toaster?.openToaster}
            delay={ANIMATION_DELAY.TOASTER_TOGGLE}
            disabled={isDisableAnimation}
          >
            <ToasterStyled
              ref={ref}
              className={classNames('toaster', toasterClass, toasterType)}
              contentMaxHeight={contentMaxHeight}
            >
              <TooltipProvider>
                <Header isDrag={isDrag} currentPage={currentPage} />
                <ThemeProvider theme={{ contentMaxHeight }}>
                  <ContentWrap
                    className={classNames(
                      'toaster-container',
                      toasterClass,
                      toasterType,
                      {
                        enabledAnimation: !isDisableAnimation,
                      },
                    )}
                    contentHeight={contentHeight}
                    width={toasterWidth - 32}
                  >
                    <ShearAnimation
                      currentPage={currentPage}
                      onResizeContent={setContentHeight}
                      disabled={isDisableAnimation}
                    />
                  </ContentWrap>
                </ThemeProvider>
              </TooltipProvider>
            </ToasterStyled>
          </Animation>
        </ToasterPosition>
      </Wrap>
    </DraggableLayout>
  );
};
