import React from 'react';

import { useTranslation } from '@@hooks/useTranslation';
import useToaster from '@@contexts/toaster/hooks/useToaster';
import { Button, BUTTON_TYPES } from '@@components/UI/Button';
import { ANIMATED_ICONS, AnimatedIcon } from '@@components/UI/AnimatedIcon';
import { ApiErrorContainer } from './ApiError.styled';

export const ERROR_TYPE = {
  NON_ECOMMERCE: 'non-ecommerce',
  SCAN_ERROR: 'scan-error',
};

const ERROR_DATA = {
  [ERROR_TYPE.NON_ECOMMERCE]: {
    icon: (
      <AnimatedIcon
        icon={ANIMATED_ICONS.info}
        size={48}
        delay={1000}
        color="ORANGE"
      />
    ),
    title: 'notEcommerceTitle',
    description: 'notEcommerceDescription',
    button: 'notEcommerceButton',
    closeType: 'default',
  },
  [ERROR_TYPE.SCAN_ERROR]: {
    icon: (
      <AnimatedIcon
        icon={ANIMATED_ICONS.info}
        size={48}
        delay={1000}
        color="ORANGE"
      />
    ),
    title: 'apiErrorTitle',
    description: 'apiErrorDescription',
    button: 'apiErrorButton',
    closeType: 'default',
  },
};

export const ApiError = ({ ext, loading, type = ERROR_TYPE.NON_ECOMMERCE }) => {
  const toaster = useToaster();
  const { t } = useTranslation();

  const data = ERROR_DATA[type];

  const closeHandler = async () => {
    const closeType = data?.closeType || 'default';
    const isOpenToaster = toaster.openToaster;

    if (closeType === 'default' && isOpenToaster) {
      toaster.showToaster(false);
      return;
    }
    if (closeType === 'default' && !isOpenToaster) {
      window.close();
      return;
    }
    if (loading || ext) {
      window.close();
    }
  };

  return (
    <ApiErrorContainer>
      {data.icon}
      <h1 className="error__title">{t(data.title)}</h1>
      <p className="error__description">{t(data.description)}</p>
      <Button type={BUTTON_TYPES.GREEN} onClick={closeHandler} isSmall>
        {t(data.button)}
      </Button>
    </ApiErrorContainer>
  );
};
