import React, { useEffect, useMemo } from 'react';
import { Loader } from '@@components/UI/AnimationIcons/Loader';
import { Title } from '@@components/UI/Title';
import { Button, BUTTON_TYPES } from '@@components/UI/Button';
import ArrowRight from '@@svg/arrowRight.svg';

import useRouter from '@@contexts/router/hooks/useRouter';
import {
  NON_GREEN_LOADING_SCAN_ROUTE,
  SCAN_RESULTS_ROUTE,
} from '@@constants/routes';
import useToaster from '@@contexts/toaster/hooks/useToaster';
import { useTranslation } from '@@hooks/useTranslation';
import classnames from 'classnames';
import { useSetAutoToaster } from '@@hooks/useSetAutoToaster';
import { getButtonText } from '@@utils/antifraude/getButtonText';
import { useAntifraude } from '@@hooks/useAntifraude';
import { ANIMATED_ICONS, AnimatedIcon } from '@@components/UI/AnimatedIcon';
import { Container, Wrap } from './Antifraude.styled';

export const Antifraude = () => {
  const { t } = useTranslation();
  const toaster = useToaster();
  const router = useRouter();

  const handlerSetAutoToaster = useSetAutoToaster();
  const { handlerCloseToaster, handlerOpenAffiliateLink } = useAntifraude();

  const { domainColor: currentColor, scanLoading, scanResult } = toaster;
  const domainColor = currentColor || 'GREEN';

  const data = useMemo(() => {
    const isOutlineButton = domainColor !== 'GREEN';
    const buttonText = getButtonText({ domainColor, isOutlineButton });
    const titleText = scanLoading
      ? 'securityTitle_DEFAULT'
      : `securityTitle_${domainColor}`;

    return {
      isOutlineButton,
      buttonType: isOutlineButton ? BUTTON_TYPES.OUTLINE : BUTTON_TYPES.GREEN,
      buttonClass: isOutlineButton ? 'outline' : 'green',
      isGreenButton: !isOutlineButton,
      loading: scanLoading,
      color: domainColor,
      title: titleText,
      button: buttonText,
    };
  }, [domainColor, scanLoading]);

  // useEffect(() => {
  //   handlerSetAutoToaster();
  // }, []);

  const handlerClick = async () => {
    if (data.color !== 'GREEN') {
      if (!scanResult || scanResult?.isDomainCacheExpired) {
        router.changePage(NON_GREEN_LOADING_SCAN_ROUTE);
        return;
      }
      router.changePage(SCAN_RESULTS_ROUTE);
      return;
    }

    toaster.setContinuePressed(true);
    await handlerSetAutoToaster();
    await handlerOpenAffiliateLink();
    await handlerCloseToaster();
  };

  const isShowButton = !data.loading;

  return (
    <Wrap>
      <Container>
        {data.loading && <Loader size={60} />}
        {!data.loading && (
          <AnimatedIcon
            icon={ANIMATED_ICONS.security}
            color={data.color}
            size={60}
          />
        )}
        <Title
          color={data.loading ? 'GREEN' : data.color}
          text={t(data.title)}
          size="H_20"
        />
      </Container>
      {isShowButton && (
        <Button
          type={data.buttonType}
          className={classnames('antifraude-button', data.buttonClass)}
          onClick={handlerClick}
          width="auto"
          isSmall
        >
          <span>{t(data.button)}</span>
          {data.isOutlineButton && (
            <ArrowRight width={24} height={24} fill="currentColor" />
          )}
        </Button>
      )}
    </Wrap>
  );
};
