import React from 'react';
import classnames from 'classnames';
import ShowMoreIcon from '@@svg/arrowDown.svg';
import { ShowMoreButtonStyled } from '@@components/UI/ShowMoreButton/ShowMoreButton.styled';

export const ShowMoreButton = ({ opened, containerRef, onClick }) => {
  const handlerOpen = () => {
    if (opened && containerRef) {
      containerRef.current.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
    }
    onClick(!opened);
  };

  return (
    <ShowMoreButtonStyled className={classnames({ open: opened })}>
      <button
        type="button"
        className="show-more--button"
        onClick={handlerOpen}
        aria-label="Show more information button"
      >
        <ShowMoreIcon
          className="icon"
          width={32}
          height={32}
          fill="currentColor"
        />
      </button>
    </ShowMoreButtonStyled>
  );
};
