import React, { useMemo } from 'react';
import { StartScanDomainStyled } from '@@content/Toaster/pages/StartScanDomain/StartScanDomain.styled';
import { Button, BUTTON_TYPES } from '@@components/UI/Button';
import useToaster from '@@contexts/toaster/hooks/useToaster';
import useRouter from '@@contexts/router/hooks/useRouter';
import {
  LOADING_SCAN_ROUTE,
  PHISHING_EXCLUSION_SCAN_ROUTE,
} from '@@constants/routes';
import { useSetAutoToaster } from '@@hooks/useSetAutoToaster';
import { useTranslation } from '@@hooks/useTranslation';
import { checkEbayDomain } from '@@utils/exclusion/checkEbaywidthQuery';
import { useAntifraude } from '@@hooks/useAntifraude';
import { ANIMATED_ICONS, AnimatedIcon } from '@@components/UI/AnimatedIcon';
import parse from 'html-react-parser';

export const StartScanDomain = ({ phishingExclusion }) => {
  const router = useRouter();
  const toaster = useToaster();
  const { t } = useTranslation();
  const { handlerOpenAffiliateLink } = useAntifraude();

  const domain = toaster.currentDomain;

  const handlerSetAutoToaster = useSetAutoToaster();

  const handlerClick = async () => {
    handlerSetAutoToaster();

    if (phishingExclusion) {
      await handlerOpenAffiliateLink();
      router.changePage(PHISHING_EXCLUSION_SCAN_ROUTE);
      router.changePreviousPage(PHISHING_EXCLUSION_SCAN_ROUTE);
      return;
    }
    router.changePage(LOADING_SCAN_ROUTE);
    router.changePreviousPage(LOADING_SCAN_ROUTE);
  };

  const data = useMemo(() => {
    if (phishingExclusion) {
      return {
        title: 'startScanExclusionTitle',
        description: 'startScanExclusionDescription',
        button: 'startScanExclusionButton',
      };
    }
    return {
      title: 'startScanDefaultTitle',
      description: 'startScanDefaultDescription',
      button: 'startScanDefaultButton',
    };
  }, [phishingExclusion]);

  const isEbayDomain = checkEbayDomain(domain);

  const isShowDomainInButton = isEbayDomain && phishingExclusion;

  return (
    <StartScanDomainStyled>
      <div className="scan-domain__content">
        <AnimatedIcon icon={ANIMATED_ICONS.security} size={60} />
        <div className="scan-domain__text-wrap">
          <h2 className="scan-domain__title">{parse(t(data.title, domain))}</h2>
          <p className="scan-domain__description">
            {parse(t(data.description))}
          </p>
        </div>
      </div>
      <Button type={BUTTON_TYPES.GREEN} onClick={handlerClick} isSmall>
        {t(data.button, isShowDomainInButton ? ` d'${domain}` : '')}
      </Button>
    </StartScanDomainStyled>
  );
};
