import React, { useEffect } from 'react';
import { FvPlusPage } from '@@components/FvPlus/FvPlusPage';
import RemovingThreatIcon from '@@svg/fvPlus/removingThreat.svg';
import { RemovingThreatStyled } from '@@content/Toaster/pages/RemovingThreat/RemovingThreat.styled';
import { useSetAutoToaster } from '@@hooks/useSetAutoToaster';

const Icon = () => (
  <RemovingThreatStyled>
    <RemovingThreatIcon />
  </RemovingThreatStyled>
);

export const RemovingThreat = () => {
  const handlerSetAutoToaster = useSetAutoToaster();

  useEffect(() => {
    handlerSetAutoToaster();
  }, []);

  return (
    <FvPlusPage
      title="Votre ordinateur est en danger"
      description="Vous avez visité un site web malveillant hier et vous avez 6 menaces de sécurité actives. Réparons-les."
      button="Résoudre les menaces"
      icon={<Icon />}
    />
  );
};
