import React, { useEffect, useRef, useState } from 'react';
import { Loader } from '@@components/UI/AnimationIcons/Loader';
import { useTranslation } from '@@hooks/useTranslation';
import useToaster from '@@contexts/toaster/hooks/useToaster';
import { Coupon } from '@@components/UI/Coupon';
import { autoApplyCoupons } from '@@content/utils/checkout/autoApply';
import useRouter from '@@contexts/router/hooks/useRouter';
import {
  ACTIVE_CASHBACK_ROUTE,
  PRODUCT_DELIVERY_VIEW_ROUTE,
} from '@@constants/routes';
import { createModal } from '@@content/utils/checkout/createModal';
import { Title } from '@@components/UI/Title';
import { getAutoApplyNewRoute } from '@@utils/checkout/getAutoApplyNewRoute';
import { SessionOffers } from '@@services/SessionOffers';
import parse from 'html-react-parser';
import { getSavedAffiliateActivated } from '../../../../../background/scripts/affiliateActivated';
import { LoadingCouponsToasterStyled } from './LoadingCouponsToaster.styled';

const descriptions = [
  'loaderDescrAutoApply1',
  'loaderDescrAutoApply2',
  'loaderDescrAutoApply3',
];

export const LoadingCouponsToaster = () => {
  const { t } = useTranslation();
  const toaster = useToaster();
  const router = useRouter();
  const [offersCache, setOffersCache] = useState({ ...toaster.offersCache });
  const [progressDuration, setProgressDuration] = useState(0);
  const [progressWidth, setProgressWidth] = useState(0);
  const startCount = useRef(offersCache?.codeOffers?.length || 0);
  const [textDescription, setTextDescription] = useState(descriptions);

  const handlerCouponLoading = (result) => {
    setOffersCache((prev) => ({
      ...prev,
      bestCoupon: result.bestCoupon,
      codeOffers: result.codeOffers,
      detectDiffPrice: result.detectDiffPrice,
      oldPrice: result.oldPrice,
      price: result.price,
      sale: result.sale,
      totalPrice: result.totalPrice,
    }));
    setTextDescription((prev) => {
      const current = prev.splice(0, 1)[0];
      return [...prev, current];
    });
  };

  const showNextToaster = async (result) => {
    if (window.fv_show_toaster === false) return;

    const cashbackAffiliateActivated = await getSavedAffiliateActivated(
      toaster.currentDomain,
      'cashback',
    );

    const isActivatedCashback =
      !!result?.cashback?.[0] && cashbackAffiliateActivated.status;

    const isHasCouponsToActivate =
      result?.codeOffers && !!result.codeOffers.length;

    const isHasActiveCoupon = !!result?.code?.length && !!result?.sale?.length;

    if (isHasCouponsToActivate) {
      handlerCouponLoading(result);
      return;
    }
    SessionOffers.remove();
    toaster.setOffersCache(result);

    const route = getAutoApplyNewRoute({
      isActivatedCashback,
      isHasActiveCoupon,
    });
    const uniqueIdFriendly = toaster?.domainOption?.uniqueIdFriendly || true;

    // const nextRoute =
    //   isActivatedCashback && uniqueIdFriendly
    //     ? ACTIVE_CASHBACK_ROUTE
    //     : PRODUCT_DELIVERY_VIEW_ROUTE;

    router.setDefaultPage(PRODUCT_DELIVERY_VIEW_ROUTE);
    createModal({ router, route });
  };

  useEffect(() => {
    (async () => {
      const result = await autoApplyCoupons(
        offersCache,
        setProgressDuration,
        startCount.current,
      );
      showNextToaster(result);
    })();
  }, [offersCache.codeOffers]);

  useEffect(() => {
    const codeOffers = offersCache.codeOffers || [];
    const { counter } = toaster.offersCache;

    setProgressDuration(codeOffers.length * 6);
    setProgressWidth(((counter - codeOffers.length) * 100) / counter);
  }, []);

  if (!offersCache?.codeOffers?.length) {
    return null;
  }

  return (
    <LoadingCouponsToasterStyled>
      <div className="loader">
        <Loader size={140} />
      </div>
      <div className="title">
        {parse(
          t('loaderTitleAutoApply', offersCache.codeOffers.length.toString()),
        )}
      </div>
      <Title className="description" text={t(textDescription[0])} />

      <div className="coupons">
        {offersCache.codeOffers.slice(0, 2).map((item, i) => (
          <Coupon
            key={`${item}_${i}`}
            code={item}
            classNames={i > 0 ? 'coupon-second' : 'coupon-first'}
          />
        ))}
      </div>
      <div
        className="progress"
        style={{
          '--progress': `${progressDuration}s`,
          '--width': `${progressWidth}%`,
        }}
      />
    </LoadingCouponsToasterStyled>
  );
};
