import React from 'react';
import useToaster from '@@contexts/toaster/hooks/useToaster';
import { DomainViewStyled } from './DomainView.styled';

export const DomainView = () => {
  const toaster = useToaster();
  const domain = toaster.currentDomain;

  return <DomainViewStyled>{domain}</DomainViewStyled>;
};
