import React from 'react';
import { useTranslation } from '@@hooks/useTranslation';
import useToaster from '@@contexts/toaster/hooks/useToaster';
import { priceReplace } from '@@content/utils/checkout/priceReplace';

import parse from 'html-react-parser';
import { FindBestPriceStyled } from './FindBestPrice.styled';

export const FindBestPrice = () => {
  const { t } = useTranslation();
  const toaster = useToaster();

  const { offersCache } = toaster;

  const salePercent = () => {
    const oldPrice = priceReplace(offersCache?.oldPrice || '1');
    const sale = priceReplace(offersCache?.sale || '1');
    return `${((parseFloat(sale) * 100) / parseFloat(oldPrice)).toFixed(0)}%`;
  };

  return (
    <FindBestPriceStyled>
      <div className="title">
        {parse(t('successAutoApplyTitle', toaster.offersCache.sale))}
      </div>
      <div className="content">
        <div className="content-info">
          <div className="content-info-row">
            <div className="content-info-row-name">
              {t('successAutoApplyInitPrice')}
            </div>
            <div className="content-info-row-result">
              {toaster.offersCache.oldPrice}
            </div>
          </div>
          <div className="content-info-row">
            <div className="content-info-row-result-wrap">
              <div className="content-info-row-result-coupon">
                <span>{toaster.offersCache.bestCoupon}</span>
              </div>
              <div className="content-info-row-result-sale">
                -{salePercent()}
              </div>
            </div>
            <div className="content-info-row-result green">
              - {toaster.offersCache.sale}
            </div>
          </div>
        </div>
        <div className="content-result">
          <div className="content-result-title">
            {t('successAutoApplyFinalPrice')}
          </div>
          <p className="content-result-price">
            {toaster.offersCache.totalPrice}
          </p>
        </div>
      </div>
    </FindBestPriceStyled>
  );
};
