import React, { useEffect } from 'react';
import ClockIcon from '@@svg/clock.svg';
import useToaster from '@@contexts/toaster/hooks/useToaster';
import { ScanProgressStyled } from './ScanProgress.styled';

function extractNumber(string) {
  const numberString = string.replace('%', '');
  return parseFloat(numberString);
}

function calculatePercentagePerSecond({ timeToWait, numberInQueue, result }) {
  const totalSeconds = (timeToWait * numberInQueue) / 1000;
  const percentagePerSecond = 100 / totalSeconds;
  return percentagePerSecond + result;
}

export const ScanProgress = ({ remaining = 10 }) => {
  const toaster = useToaster();

  const { timeToWait, numberInQueue } = toaster;

  useEffect(() => {
    if (toaster.percentProgress === '95%') toaster.setPercentProgress(`100%`);
  }, [toaster.percentProgress]);

  useEffect(() => {
    const interval = setInterval(() => {
      if (toaster.percentProgress !== '100%') {
        const result = extractNumber(toaster.percentProgress);

        const percent = calculatePercentagePerSecond({
          timeToWait,
          numberInQueue,
          result,
        });
        toaster.setPercentProgress(`${percent}%`);
      }
    }, 2000);

    return () => {
      clearInterval(interval);
    };
  }, [toaster.percentProgress]);

  return (
    <ScanProgressStyled>
      <div className="scan-progress">
        <div className="scan-progress__content">
          <div className="scan-progress__title">
            Analyse des modes de paiement...
          </div>
          <div className="progress-bar">
            <div
              className="progress-bar__line"
              style={{
                width: toaster.percentProgress,
              }}
            />
          </div>
          <div className="scan-progress__time">
            <ClockIcon width={16} height={16} fill="#74718A" />
            <p className="scan-progress__time-text">
              Temps restant :<span>{remaining} sec</span>
            </p>
          </div>
        </div>
      </div>
    </ScanProgressStyled>
  );
};
