import React from 'react';
import useToaster from '@@contexts/toaster/hooks/useToaster';
import { callBrowser } from '@@utils/utils';
import { useTranslation } from '@@hooks/useTranslation';
import { Button, BUTTON_TYPES } from '@@components/UI/Button';
import useRouter from '@@contexts/router/hooks/useRouter';
import { LOADING_AUTO_APPLY_ROUTE } from '@@constants/routes';
import { createModal } from '@@content/utils/checkout/createModal';
import { useSetAutoToaster } from '@@hooks/useSetAutoToaster';
import { ANIMATION_DELAY } from '@@constants/anomationDelay';

import parse from 'html-react-parser';
import { InitCouponsToasterStyled } from './InitCouponsToaster.styled';

export const InitCouponsToaster = () => {
  const toaster = useToaster();
  const router = useRouter();

  const { t } = useTranslation();
  const handlerSetAutoToaster = useSetAutoToaster();
  const { offersCache } = toaster;

  const couponsCount = offersCache.allOffers.length;

  const handlerOpenModal = () => {
    router.changePage(LOADING_AUTO_APPLY_ROUTE);
    toaster.showToaster(true);
  };

  const handlerClick = () => {
    handlerSetAutoToaster();
    toaster.showToaster(false);

    setTimeout(() => {
      createModal({
        router,
        handlerOpenModal,
        route: LOADING_AUTO_APPLY_ROUTE,
        isCustomStyle: false,
      });
    }, ANIMATION_DELAY.MODAL_SHOW);
  };

  const title = `${couponsCount} ${t(
    'titleCountOffersBeforeApply',
    couponsCount > 1 ? 's' : '',
  )}  `;

  return (
    <InitCouponsToasterStyled>
      <div className="init-coupons__wrap">
        <div className="init-coupons__info">
          <h1 className="init-coupons__title">{parse(title)}</h1>
          <p className="init-coupons__description">
            {t('descriptionOffersBeforeApply')}
          </p>
        </div>
        <img
          className="init-coupons__image"
          src={callBrowser().runtime.getURL(`images/money.png`)}
          width={81}
          height={70}
          alt="Money"
        />
      </div>
      <Button type={BUTTON_TYPES.GREEN} onClick={handlerClick} isSmall>
        {t('buttonOffersBeforeApply')}
      </Button>
    </InitCouponsToasterStyled>
  );
};
