import React, { memo, useState } from 'react';
import useToaster from '@@contexts/toaster/hooks/useToaster';
import useRouter from '@@contexts/router/hooks/useRouter';
import {
  AFTER_13_DAY_VISIT_FIRST,
  AFTER_13_DAY_VISIT_SECOND,
  AFTER_14_DAY_DANGEROUS_FIRST,
  AFTER_14_DAY_DANGEROUS_SECOND,
  AFTER_14_DAY_EMAIL,
  AFTER_15_DAY_EMAIL,
  AFTER_1_DAY_VISIT,
  AFTER_5_DAY_EMAIL,
  AFTER_5_DAY_VISIT,
  ANTIFRAUDE_ROUTE,
  BEFORE_RESULTS_ROUTER,
  DOMAIN_SCAN_ROUTE,
  MAIN_ROUTE,
  NUDGE_CAMPAIGN_EMAIL,
  NUDGE_CAMPAIGN_VISIT,
  PHISHING_EXCLUSION_RESULT_ROUTE,
  PHISHING_EXCLUSION_ROUTE,
  PRODUCT_DELIVERY_VIEW_ROUTE,
  PROMOTION_TARGET_USER_EMAIL,
  PROMOTION_TARGET_USER_VISIT,
} from '@@constants/routes';
import classNames from 'classnames';

import { closeModal } from '@@utils/checkout/closeModal';
import { useSetAutoToaster } from '@@hooks/useSetAutoToaster';
import { useDeliveryViewClick } from '@@hooks/useDeliveryViewClick';
import { useAntifraude } from '@@hooks/useAntifraude';
import { useResultApplyAffiliateActivated } from '@@hooks/useResultApplyAffiliateActivated';
import { DomainView } from '@@components/UI/DomainView/DomainView';

import ENV from '@@constants/globals';
import { Menu } from '@@components/Menu';
import { Animation } from '@@components/UI/Animation';

import { getToasterType } from '@@utils/utils';
import LogoIcon from '@@svg/logo.svg';
import CloseIcon from '@@svg/close.svg';
import SettingsIcon from '@@svg/settings.svg';

import { checkFnacExclusion } from '@@utils/checkFnacExxlusion';
import { ButtonWrap, HeaderWrap, SettingsContainer } from './Header.styled';

const TOGGLE_TOASTER = [
  AFTER_1_DAY_VISIT,
  AFTER_5_DAY_EMAIL,
  AFTER_5_DAY_VISIT,
  AFTER_13_DAY_VISIT_FIRST,
  AFTER_13_DAY_VISIT_SECOND,
  AFTER_14_DAY_EMAIL,
  AFTER_14_DAY_DANGEROUS_FIRST,
  AFTER_14_DAY_DANGEROUS_SECOND,
  AFTER_15_DAY_EMAIL,
  PROMOTION_TARGET_USER_EMAIL,
  PROMOTION_TARGET_USER_VISIT,
  NUDGE_CAMPAIGN_VISIT,
  NUDGE_CAMPAIGN_EMAIL,
];

export const Header = memo(({ isDrag, currentPage }) => {
  const toaster = useToaster();
  const route = useRouter();

  const handlerSetAutoToaster = useSetAutoToaster();
  const handlerDeliveryViewClick = useDeliveryViewClick();

  const { handlerAffiliateActivated } = useResultApplyAffiliateActivated();

  const {
    handlerCloseToaster: handlerCloseAntifraudeToaster,
    handlerOpenAffiliateLink,
  } = useAntifraude();

  const [opened, setOpened] = useState(false);

  const {
    isModal: isModalType,
    isAlert: isAlertType,
    className: toasterClass,
  } = getToasterType(currentPage);

  const isShowMenu = !(isModalType || isAlertType);

  const handlerOpenMenu = (isOpen) => () => {
    setOpened(isOpen);
  };

  const handlerCloseToaster = async () => {
    const isToggleToaster = TOGGLE_TOASTER.includes(currentPage);

    if (isToggleToaster) {
      toaster.toggleAlert();
    } else {
      toaster.closeToaster();
    }

    handlerSetAutoToaster();

    if (currentPage === ANTIFRAUDE_ROUTE) {
      await handlerOpenAffiliateLink();
      handlerCloseAntifraudeToaster();
      return;
    }

    if (
      currentPage === PHISHING_EXCLUSION_ROUTE ||
      currentPage === DOMAIN_SCAN_ROUTE
    ) {
      const isFnacExclusion = checkFnacExclusion(toaster.currentDomain);

      if (!isFnacExclusion) {
        await handlerOpenAffiliateLink();
      }

      handlerCloseAntifraudeToaster({
        defaultRoute: currentPage,
      });
      return;
    }

    if (
      currentPage === BEFORE_RESULTS_ROUTER ||
      currentPage === PHISHING_EXCLUSION_RESULT_ROUTE
    ) {
      handlerCloseAntifraudeToaster({
        defaultRoute: currentPage,
      });
      return;
    }

    // if (currentPage === NO_UNIQUE_ID_FRIENDLY_ACTIVE_OFFERS_ROUTE) {
    //   handlerCashbackActivate();
    //   return;
    // }

    if (isModalType) {
      closeModal();
      const isOpenToaster = await handlerAffiliateActivated();
      setTimeout(() => {
        toaster.showToaster(isOpenToaster);
      }, 1500);
      return;
    }

    if (currentPage === PRODUCT_DELIVERY_VIEW_ROUTE) {
      handlerDeliveryViewClick();
    }
  };

  // ---------- DEBUGGING POPUP DEV ONLY ----------
  const isProduction = ENV.API_ENV === 'PROD';

  const mode = classNames({
    debug: !isProduction,
  });

  const handlerLogoClick = () => {
    if (isProduction) {
      return;
    }
    route.changePage(MAIN_ROUTE);
  };
  // --------------------------------------------

  const isDisableAnimation = toaster.animationFreeze > 5;

  return (
    <HeaderWrap
      id="popupDragHandler"
      className={classNames(mode, toasterClass, {
        isDrag: isDrag && !isModalType,
      })}
    >
      {!isModalType && (
        <LogoIcon
          className="logo"
          width={116}
          height={24}
          fill="#ffffff"
          onClick={handlerLogoClick}
        />
      )}
      {isModalType && <DomainView />}

      <div className="buttonsContainer">
        {isShowMenu && (
          <SettingsContainer
            onMouseEnter={() => setOpened(true)}
            onMouseLeave={() => setOpened(false)}
          >
            <ButtonWrap onClick={handlerOpenMenu}>
              <SettingsIcon width={20} height={20} fill="currentColor" />
            </ButtonWrap>

            <Animation
              delay={400}
              opened={opened}
              disabled={isDisableAnimation}
            >
              <Menu onClick={setOpened} />
            </Animation>
          </SettingsContainer>
        )}

        <ButtonWrap onClick={handlerCloseToaster}>
          <CloseIcon width={20} height={20} fill="currentColor" />
        </ButtonWrap>
      </div>
    </HeaderWrap>
  );
});
