import React, { createContext, useState } from 'react';
import {
  TooltipContainer,
  TooltipStyled,
} from '@@components/Tooltip/Tooltip.styled';
// eslint-disable-next-line import/no-cycle
import { DEFAULT_OPTIONS, getTooltipPosition } from '.';

const TooltipContext = createContext({});

const TooltipProvider = ({ children, options = {} }) => {
  const [OPTIONS, _] = useState({ ...DEFAULT_OPTIONS, ...options });
  const [text, setText] = useState(null);

  const [tooltipPosition, setTooltipPosition] = useState('bottom');
  const [offsetX, setOffsetX] = useState(0);
  const [offsetY, setOffsetY] = useState(0);

  const onMouseMove = (e) => {
    if (!text) {
      return;
    }

    const rect = e.currentTarget.getBoundingClientRect();
    const { x, y, position } = getTooltipPosition({
      rect,
      clientX: e.clientX,
      clientY: e.clientY,
      ...OPTIONS,
    });
    setOffsetX(x);
    setOffsetY(y);
    setTooltipPosition(position);
  };

  return (
    <TooltipContext.Provider value={setText}>
      <TooltipContainer onMouseMove={onMouseMove}>
        {children}
        {!!text && (
          <TooltipStyled
            className={tooltipPosition}
            style={{ top: offsetY, left: offsetX, maxWidth: OPTIONS.maxWidth }}
          >
            {text}
          </TooltipStyled>
        )}
      </TooltipContainer>
    </TooltipContext.Provider>
  );
};

export { TooltipContext, TooltipProvider };
