import { createGlobalStyle } from 'styled-components';
import {
  CLASS_BACKING,
  CLASS_COOKIE,
  CLASS_COOKIE_OVERLAY,
  FV_MODAL_CLASS,
  FV_POPUP_ID,
} from '@@constants/common';

const ContentGlobalStyle = createGlobalStyle`
  :host {
    all: initial;
    }

  *,
  *::before,
  *::after {
    box-sizing: border-box;
    direction: ltr;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
    
    &::-webkit-scrollbar {
      display: none;
    }
  }

  .dragContainer {
    font-family: 'PoppinsFV', sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0;
  
 }

  a {
    margin: 0;
    padding: 0;
    text-decoration: none;
    color: currentColor;
  }

  button {
    border: none;
    margin: 0;
    padding: 0;
    width: 100%;
    overflow: visible;
    background: transparent;
    color: inherit;
    font: inherit;
  }
  input {
    border: none;
    outline: none;
    border-radius: 0;
    font: inherit;
    font-size: 100%;
    line-height: 100%;
  }
  select{
    width: 100%;
    border: none;
    outline: none;
    border-radius: 0;
    background-color: transparent;
    font: inherit;
    font-size: 100%;
    line-height: 100%;
  }
  svg{
    display: block
  }

  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus,
  input:-webkit-autofill:active{
    -webkit-box-shadow: 0 0 0 30px #FFFFFF inset !important;
  }
  
  ul {
    font-size: 100%;
    font: inherit;
    padding: 0;
    margin: 0;
    list-style-type: none;
  }



  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
  }
  

 button {
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  border-radius: 0;
 }

 p, h1, h2, h3 {
  margin: 0;
 }
 
 .dragContainer.${FV_MODAL_CLASS} {
   transform: none !important;
   position: fixed;
   top:0;
   left:0;
   width: 100%;
   height: 100%;

   
  &.mount{
    .${CLASS_BACKING} {
      transition: opacity 250ms;
      opacity: 1;
    }
    .toaster-position {
      transition: opacity 500ms, transform 500ms;
      transform: translate(-50%, -50%) scale(1);
      transform-origin: center;
      opacity: 1;
    }
   }

   &.unmount{
     .${CLASS_BACKING} {
       transition: opacity 250ms;
       opacity: 0;
     }
     .toaster-position {
       transition: opacity 500ms, transform 500ms;
       transform: translate(-50%, -50%) scale(0.5);
       transform-origin: center;
       opacity: 0;
     }
   }
   
   
   
   .${CLASS_BACKING} {
     position: relative;
     width: 100%;
     height: 100%;
     background: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3));
     opacity: 0;
     z-index: -1;
   }
   
   
   .toaster-position {
     top:50%;
     left: 50%;
     bottom: auto;
     right: auto;
     opacity: 0;

     transform: translate(-50%, -50%) scale(0.5);
   }
   
   .toaster{
     top:auto;
     left:auto;
     bottom: auto;
     right: auto;
     transition: none;
     transform: none;
     max-height: 90vh;
   }
   
   .toaster-container{
     max-height: calc(100% - 40px);
     overflow-y: scroll;
   }
 }



  .dragContainer.${CLASS_COOKIE} {
    transform: none !important;
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height: 100%;


    &.mount{
      .${CLASS_COOKIE_OVERLAY} {
        transition: opacity 250ms;
        opacity: 1;
      }
      .toaster-position {
        transition: opacity 500ms, transform 500ms;
        transform: translate(-50%, -50%) scale(1);
        transform-origin: center;
        opacity: 1;
      }
    }

    &.unmount{
      .${CLASS_COOKIE_OVERLAY} {
        transition: opacity 250ms;
        opacity: 0;
      }
      .toaster-position {
        transition: opacity 500ms, transform 500ms;
        transform: translate(-50%, -50%) scale(0.5);
        transform-origin: center;
        opacity: 0;
      }
    }



    .${CLASS_COOKIE_OVERLAY} {
      position: relative;
      pointer-events: none;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.7); 
      opacity: 0;
      z-index: -1;
    }


    .toaster-position {
      top:50%;
      left: 50%;
      bottom: auto;
      right: auto;
      opacity: 0;

      transform: translate(-50%, -50%) scale(0.5);
    }

    .toaster{
      top:auto;
      left:auto;
      bottom: auto;
      right: auto;
      transition: none;
      transform: none;
      max-height: 90vh;
    }

    .toaster-container{
      max-height: calc(100% - 40px);
      overflow-y: scroll;
    }
  }


`;

export default ContentGlobalStyle;
